
.contenedor {
  overflow: hidden;
  color: white;
  text-align: center;


  height: 81vh;
  margin-top: 3vh;
 
}
.contenedorRegistro {
  overflow: hidden;
  color: white;
  text-align: center;


  margin-top: 3vh;
 
}
.headerRegistro{
  color: black;
  position: relative;

  text-align: center;
}
.Formulario {
  background: rgba(176, 137, 104, 0.8);

  width: 30vw;


  margin-left: 35vw;
  border-radius: 12px;

  box-shadow: 0px 0px 15px 2px rgba(221, 184, 146, 0.8);
}

.FormularioRegistro {
  background: rgba(176, 137, 104, 0.8);

  width: 30vw;


position: relative;
display: inline-block;
  border-radius: 12px;

  box-shadow: 0px 0px 15px 2px rgba(221, 184, 146, 0.8);
}
.control {
  margin: 2%;
  text-align: center;
  position: relative;
  display: block;
}

.control label {
  display: block;
  font-weight: bold;
  font-size: 1.5vw;
  padding-top: 2%;
}

.control input,
.control select {
  display: block;
  width: 80%;
  font: inherit;
  padding: 0.15rem;
  border-radius: 8px;
  border: 1px solid #ccc;
  text-align: center;
  margin: 0 auto;
}

.control input:focus,
.control select:focus {
  outline: none;
  border-color: #230052;
}

.button {
  margin-bottom: 3%;
}
