  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
  border-radius: 12px;

  color: white;
  width: auto;

}

.SpanIzquierdo{
text-align: justify;

display: inline-block;

}
.SpanDerecha{
  display: inline-flex;
  }

.SpanDerecha p{
width: 13vw;
