* {
  box-sizing: border-box;
}

body {
  margin: 0;
  background-color: #272727;
}

.scroll {
  overflow-y: auto;
  height: 100vh;
}
#root::-webkit-scrollbar {
  width: 10px;
  height: 10px;
}
#root::-webkit-scrollbar-thumb {
  background: linear-gradient(to bottom right, #E6CCB2 0%, #7F5539 100%);
  border-radius: 5px;
  cursor: pointer;

}

#root::-webkit-scrollbar-track {
  background-color: #ddd;
  border: 1px solid #ccc;
  border-radius: 5px;

}

