body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
}
#divHeader {
  width: 100vw;
  height: 15vh;
  overflow-y: hidden;
  overflow-x: hidden;
  display: inline-block;
}

#HeaderTitulo {
  width: 20vw;
  color: #fff;
  text-align: center;
  height: 70%;
  padding: 1%;
  font-size: 1.3vw;
  float: left;
  margin-top: 1.5%;
}

#divHeader nav {
  text-align: left;
  width: 70vw;
  margin-left: 25%;
}
ul {
  list-style-type: none;
  height: auto;
  margin-top: 6vh;
  display: flex;


}
li {
 margin-left: 2.5%;
 margin-right: 2.5%;
  margin-top: 1.5%;
  
}


a {
  color: white;
  text-decoration: none;
  font-size: 1.3vw;
}
a:hover {
  color: #7F5539;
  text-decoration: underline;
 cursor: pointer;
}

#contact {
  margin-left: 15%;
  margin-top: 0;
}
