@import url('https://fonts.googleapis.com/css2?family=Montserrat&display=swap');

body{
  background-image: url(components/img/fondo.jpg);
  

}
* {
font-family: Montserrat;
  overflow-x: hidden;
  overflow-y: hidden;
  
}

button {
  font-size: 2.5vh;
  font-weight: 1000;
  cursor: pointer;
  background-color: rgba(127, 85, 57 );
  border: 1px solid black;
  color: white;
  padding: 0.75rem 2rem;
  border-radius: 20px;
}

button:focus {
  outline: none;
}

button:hover,
button:active {
  background-color: rgba(237, 224, 212);

  color: #7F5539;
}
